<template>
    <div class="content">
        <div class="content__side-bar" v-if="displayMenu">
            <sideBar></sideBar>
        </div>
        <div class="content__main">
            <card @menuFun="switchMenu"></card>
        </div>
        <div class="overlay" v-if="showOverlay" @click="hidePopup"></div>
    </div>
</template>

<script>
import SideBar from '../components/sidebar.vue';
import Card from '../components/card/card.vue';
export default {
    name: 'Vistors',
    components: {
        SideBar,
        Card
    },
    computed: {
        displayMenu() {
            return (document.body.clientWidth < 768 &&
                    !this.showOverlay) ? false : true
        }
    },
    data() {
        return {
            showOverlay: false,
            showMenu: true,
        }
    },
    methods: {
        switchMenu() {
            this.showOverlay = true;
        },
        hidePopup() {
            this.showOverlay = false;
        },
    }
}
</script>
 
