import Vue from 'vue'
import VueRouter from 'vue-router'
import login from '../views/login.vue'
import signup from '../views/signup.vue'
import visitor from '../views/visitor.vue'
import message from '../views/message.vue'
import meets from '../views/meets.vue'
import account_setting from '../views/account/account-setting.vue'
import change_email from '../views/account/changeEmail.vue'
import disableAcc from '../views/account/disableAcc.vue'
import change_pwd from '../views/account/changePwd.vue'
import meet_account from '../views/meets-account.vue'
import filter from '../views/meet-filter.vue'
import profile from '../views/profile.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'signin',
    component: login,
    meta:{
      title: 'signin'
    }
  },
  {
    path: '/signup',
    name: 'signup',
    component: signup,
    meta:{
      title: 'signup'
    }
  },
  {
    path: '/visitor',
    name: 'visitor',
    component: visitor,
    meta: {
      title: 'visitor'
    }
  },
  {
    path: '/message',
    name: 'message',
    component: message,
    meta: {
      title: 'message'
    }
  },
  {
    path: '/meets',
    name: 'meets',
    component: meets,
    meta: {
      title: 'meets'
    }
  }
  ,
  {
    path: '/account_setting',
    name: 'account_setting',
    component: account_setting,
    meta: {
      title: 'account setting'
    }
  },
  {
    path: '/meet_account',
    name: 'meet_account',
    component: meet_account,
    meta: {
      title: 'meet_account'
    }
  }
  ,
  {
    path: '/change_email',
    name: 'change_email',
    component: change_email,
    meta: {
      title: 'change email'
    }
  }
  ,
  {
    path: '/disableAcc',
    name: 'disableAcc',
    component: disableAcc,
    meta: {
      title: 'disable account'
    }
  },
  {
    path: '/change_pwd',
    name: 'change_pwd',
    component: change_pwd,
    meta: {
      title: 'change password'
    }
  },
  {
    path: '/filter',
    name: 'filter',
    component: filter,
    meta: {
      title: 'filter'
    }
  },{
    path: '/profile',
    name: 'profile',
    component: profile,
    meta: {
      title: 'profile'
    }
  }
]

const router = new VueRouter({
  routes
})
router.beforeEach((to, from, next) => {

  if (to.meta.title) {
    document.title = to.meta.title
  }
  next()
})
export default router
