<template>
<div class="content">
    <div class="content__side-bar" v-if="displayMenu">
        <sideBar></sideBar>
    </div>
    <div class="content__main">
        <div class="content__main--title">
            <div class="header__title">
                <div class="meets__title">
                    <button class="meets__title--menu" @click="switchMenu">
                        <img class="coverImg" src="../assets/icon/menu.jpg" alt="">
                    </button>
                    <router-link to="meet_account">
                        <h2 class="meets__title--item active">Meet</h2>
                    </router-link>
                    <router-link to="meets">
                        <h2 class="meets__title--item">Likes</h2>
                    </router-link>
                    <button class="meets__title--filter">
                        <img class="coverImg" src="../assets/icon/filter.png" alt="">
                    </button>
                </div>
            </div>
        </div>
        <div class="content__main--content">
            <single-card></single-card>
        </div>
    </div>
    <div class="overlay" v-if="showOverlay" @click="hidePopup"></div>
</div>
</template>

<script>
import SideBar from '../components/sidebar.vue';
import SingleCard from '../components/card/single-card.vue';
export default {
    name: 'meetAccount',
    components: {
        SideBar,
        SingleCard,
    },
    computed: {
        displayMenu() {
            return (document.body.clientWidth < 768 &&
                    !this.showOverlay) ? false : true
        }
    },
    data() {
        return {
            showOverlay: false,
            showMenu: true,
        }
    },
    methods: {
        switchMenu() {
            this.showOverlay = true;
        },
        hidePopup() {
            this.showOverlay = false;
        },
        gotoFilter() {
            this.$router.push({
                path: 'filter'
            })
        }
    }
}
</script>