<template>
<div class="content">
    <div class="content__side-bar" v-if="displayMenu">
        <sideBar></sideBar>
    </div>
    <div class="content__main">
        <div class="content__main--title">
            <div class="header__title">
                <div class="meets__title">
                    <button class="meets__title--menu" @click="switchMenu">
                        <img class="coverImg" src="../assets/icon/menu.jpg" alt="">
                    </button>
                    <router-link to="meet_account">
                        <h2 class="meets__title--item">Meet</h2>
                    </router-link>
                    <router-link to="meets">
                        <h2 class="meets__title--item active">Likes</h2>
                    </router-link>
                    <button class="meets__title--filter">
                        <img class="coverImg" src="../assets/icon/filter.png" alt="">
                    </button>
                </div>
            </div>
        </div>
        <div class="content__main--content">
           <card v-if="showNormal" :tstatus="hideTitle"></card>
           <mask-card v-if="showMask"></mask-card>
           <mk-card v-if="showMask"></mk-card>
        </div>
    </div>
    <div class="overlay" v-if="showOverlay" @click="hidePopup"></div>
</div>
</template>

<script>
import SideBar from '../components/sidebar.vue';
import Card from '../components/card/card.vue';
import MaskCard from '../components/card/maskCard.vue';
import MkCard from '../components/card/maskCard-mobile.vue';
export default {
    name: 'Meets',
    components: {
        SideBar,
        Card,
        MaskCard,
        MkCard
    },
    data() {
        return {
            hideTitle: true,
            type: 'mask',
            showOverlay: false,
            showMenu: true,
        }
    },
    computed: {
        showNormal() {
           return this.type === 'normal' || this.type == '';
        },
        showMask() {
            return this.type === 'mask';
        },
        displayMenu() {
            return (document.body.clientWidth < 768 &&
                    !this.showOverlay) ? false : true
        },
        gotoFilter() {
            this.$router.push({
                path: 'filter'
            })
        }
    },
    methods: {
        switchMenu() {
            this.showOverlay = true;
        },
        hidePopup() {
            this.showOverlay = false;
        }
    },
}
</script>

